@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');
body {
    overflow-x: hidden; /* Prevents horizontal scrolling */
}

.hero {
    position: relative;
    width: 100%;
    height: 500px;
    background-color: #004d99;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 50px 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    color: white;
}

.hero * {
    font-family: 'Press Start 2P', cursive;
}

/* Progress containers */
.progress-container {
    position: absolute;
    top: 10px;
    left: 0;
    width: 100%;
    height: 20px;
    display: flex;
    align-items: center;
    z-index: 1000;
}

.correct-container {
    top: 10px;
}

.incorrect-container {
    top: 40px;
}

.correct-bar, .incorrect-bar {
    height: 20px;
    transition: width 0.4s ease;
}

.correct-bar {
    background-color: #39FF14;
}

.incorrect-bar {
    background-color: red;
}

.progress-text {
    margin-left: 10px;
    color: white;
    font-size: 16px;
    white-space: nowrap;
}

.question-text {
    font-size: 2rem;
    color: white;
    margin: 20px 0;
    text-align: center;
}

.grade-input-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.grade-label {
    font-size: 1.5rem;
    color: white;
    text-align: center;
    margin-bottom: 40px;
}

.grade-input {
    width: 100px;
    height: 50px;
    padding: 5px;
    font-size: 1.2rem;
    text-align: center;
    color: black;
    background-color: white;
    border: 2px solid #007bff;
    border-radius: 5px;
    margin-bottom: 40px;

}

.grade-circle {
    padding: 10px 20px;
    font-size: 1.5rem;
    cursor: pointer;
    background-color: red;
    color: white;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s, transform 0.3s;
}

.grade-circle:hover {
    background-color: #45a049;
    transform: scale(1.1);
}

.option-button {
    display: inline-block;
    margin: 10px 5px;
    border-radius: 8px;
    width: auto;
    min-width: 80px;
    max-width: 150px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    background-color: red;
    color: white;
    border: none;
    font-size: 0.8rem;
    padding: 0 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.3s;
}

.option-button:hover {
    background-color: #0056b3;
    transform: scale(1.1);
}

@media (max-width: 768px) {
    .hero {
        padding: 30px 15px;
    }

    .progress-text, .option-button, .question-text, .grade-circle, .grade-label, .grade-input {
        font-size: 0.7rem;
    }


    .question-text {
        font-size: 1.5rem;
    }

    .grade-input {
        width: 50px;
        height: 50px;
        padding: 10px 0;
        margin-bottom: 20px;
    }

    .grade-label {
        font-size: 1.5rem;
    }

    .grade-circle {
        font-size: 1.5rem;
        padding: 12px 20px;
        width: 80%;
        margin: 10px auto;
    }
}
